@use '../../../scss/Foundation/mixin';

.wpcf7-response-output{
    font-size: max(calc(16/1440*100vw),16px);
    color:#fff;
    @include mixin.mq(tb){
        font-size: max(calc(16/1023*100vw),16px);
    }
    @include mixin.mq(sp){
        font-size: max(calc(14/375*100vw),14px);
    }
}