@use '../../../scss/Foundation/mixin';

.c-profile__external-link--blog{
    background:url(./img/blog-icon.png);
    width: max(calc(50/1440*100vw),40px);
    height: max(calc(50/1440*100vw),40px);
    background-size: cover;
    @include mixin.mq(sp){
        width:calc(50/375*100vw);
        height: calc(50/375*100vw);
    }
}